.features {
  @include flex(center, normal, row, wrap);
  margin-left: -15px;
  margin-right: -15px;
  padding-left: percentage(1/12);
  @include lg {
    padding-left: 0; }
  &__item {
    padding-left: 15px;
    padding-right: 15px;
    width: 33.33333%;
    margin-bottom: 45px;
    @include xl {
      margin-bottom: 30px; }
    @include md {
      width: 50%; }
    @include sm {
      margin-bottom: 15px; }
    @include xs {
      width: 100%; } } }

.feature {
  @include flex();
  @include xs;
  &__icon {
    @include image('Circle.png');
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: contain;
    flex: 0 0 110px;
    color: #5c5c5c;
    .icon {
      font-size: 134px; }
    @include md {
      flex: 0 0 100px;
      .icon {
        font-size: 100px; } }
    @include sm {
      flex: 0 0 80px;
      .icon {
        font-size: 80px; } } }
  &__content {
    padding-top: 60px;
    padding-left: 10px;
    @include xl {
      padding-top: 50px; }
    @include lg {
      padding-top: 40px; }
    @include md {
      padding-top: 30px; }
    @include sm {
      padding-top: 15px; }
    @include xs {
      padding-top: 10px; } }
  &__title {
    font-weight: 700;
    font-size: $fz-md;
    color: $c-title; }
  &__text {
    font-size: 1.125em; } }
