.social {
  .icon {
    font-size: 21px; }
  li {
    display: inline-block;
    @include notlast {
      margin-right: 15px; } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $black-light; } }
  &--lg {
    .icon {
      font-size: 30px; } }
  &--light {
    a {
      @include hover {
        color: $white; } } } }
