small {
  font-size: inherit; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 400; }

.title {
  @include css-lock(32, 60, 320, 1399, font-size);
  color: $c-title;
  @include font-title;
  display: inline;
  position: relative;
  z-index: 1;
  line-height: 1;
  @include before {
    border-bottom: 3px solid  $accent;
    position: absolute;
    bottom: .15em;
    left: 0;
    width: 100%;
    z-index: -1; }

  &--gray-lighten {
    color: $gray-lighten; }
  &--h2 {
    @include css-lock(21, 45, 320, 1399, font-size);
    @include before {
      display: none; } }
  &--h4 {
    @include css-lock(18, 21, 320, 1399, font-size); } }
