.contacts {
  position: relative;
  padding-top: 10%;
  padding-bottom: 10%;
  overflow: hidden;
  @include lg {
    padding-bottom: 20px;
    padding-top: 20%; }
  @include md {
    padding-top: 0;
    padding-bottom: 0; }
  &__map {
    @include coverdiv;
    z-index: 0;
    background-color: $c-text;
    @include md {
      position: relative;
      width: auto;
      height: auto;
      padding-top: 60%;
      margin-left: -15px;
      margin-right: -15px; }
    iframe {
      @include coverdiv; } }
  &__block {
    position: relative;
    z-index: 1;
    max-width: 500px;
    @include md {
      max-width: none;
      margin-left: -15px;
      margin-right: -15px; } } }


.contacts-block {
  background-color: $black-light;
  padding: 70px;
  @include xl {
    padding: 50px; }
  @include lg {
    padding: 40px; }
  @include md {
    padding: 30px; }
  @include sm {
    padding: 20px; }
  @include xs {
    padding: 15px; }
  &__title {
    margin-bottom: 15px;
    @include md {
      margin-bottom: 10px; } }
  &__subttl {
    margin-bottom: 55px;
    @include xl {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; } }
  &__contacts {
    margin-bottom: 45px;
    @include lg {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } } }

.contacts-list {
  .icon {
    position: relative;
    top: .15em;
    font-size: 1.2em;
    position: absolute;
    left: 0; }
  li {
    position: relative;
    padding-left: 25px;
    @include notlast {
      margin-bottom: 15px;
      @include md {
        margin-bottom: 10px; } } } }
