.footer {
  background-color: $black-light;
  &__inner {
    @include flex(flex-start, center, row, wrap); }
  &__left {
    @include sm {
      width: 100%;
      margin-bottom: 15px; } }
  &__nav {
    flex-grow: 1; }
  &__auth {
    width: 100%;
    color: $gray;
    @include flex(center, center, row, wrap);
    margin-top: -30px;
    a {
      @include tr(color .3s);
      @include hover {
        color: $accent; } }
    @include lg {
      margin-top: 0;
      padding-top: 10px; } }
  &__auth-icon {
    color: $white;
    width: 35px;
    height: 35px;
    @include flex(center, center);
    background-color: $black;
    margin-right: 10px;
    .icon {
      font-size: 20px; } }
  &__logo {}
  &__copy {
    font-size: 13px;
    color: $gray; } }
