.hero {
  position: relative;
  @include after {
    position: absolute;
    top: 0;
    bottom: 50px;
    right: 0;
    left: 65%;
    background-color: $black-light;
    @include sm {
      left: 40%;
      bottom: auto;
      height: calc(#{$header-height-sm} + 15px); }
    @include xss {
      left: 30%; } }
  &__inner {
    @include flex(flex-start, normal, row, wrap);
    margin-left: -15px;
    margin-right: -15px;
    position: relative;
    z-index: 1;
    padding-top: $header-height;
    min-height: 100vh;
    min-height: calc(var(--vh, 1vh) * 100);
    @include lg {
      padding-top: $header-height-lg; }
    @include md {
      padding-top: $header-height-md; }
    @include sm {
      padding-top: $header-height-sm;
      min-height: 0;
      padding-bottom: 30px; } }
  &__left, &__right {
    padding-left: 15px;
    padding-right: 15px; }
  &__left {
    width: percentage(5/12);
    padding-top: 30px;
    @include xl {
      width: percentage(6/12); }
    @include md {
      padding-top: 15px; }
    @include sm {
      width: 100%; } }
  &__right {
    width: percentage(7/12);
    @include xl {
      width: percentage(6/12); }
    @include sm {
      // width: 100%
      display: none; } }
  &__img {
    @include cover;
    // padding-top: 100%
    display: block;
    height: 100%;
    @include sm {
      padding-top: 100%; } }
  &__logo {
    margin-bottom: 30px;
    @include md {
      margin-bottom: 15px; } }
  &__text {
    margin-bottom: 60px;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 20px; } }
  &__btns {
    margin-bottom: 10%;
    @include flex(flex-start, center);
    @include lg {
      display: block; }
    @include sm {
      margin-bottom: 20px; } }
  &__btn {
    &:first-child {
      margin-right: 50px;
      @include xl {
        margin-right: 10px; }
      @include lg {
        margin-right: 0;
        margin-bottom: 15px; } } }
  &__social {
    .social {
      display: inline-block;
      position: relative;
      vertical-align: middle;
      margin-left: 10px; } } }

