.nav {
  @include sm {
    font-size: 1.5em; }
  @include xss {
    font-size: 1.2em; }
  li {
    display: inline-block;
    @include notlast {
      margin-right: 8%;
      @include md {
        margin-right: 20px; }
      @include sm {
        margin-right: 0; } }
    @include sm {
      display: block;
      margin-bottom: 15px; }
    @include xss {
      margin-bottom: 10px; } }
  a {
    @include tr(color .3s);
    @include hover {
      color: $black-light; }
    &.is-active {
      color: $black-light; } }

  &--footer {
    @include sm {
      font-size: $fz-sm; }
    @include xss {
      font-size: $fz-sm; }
    a {
      @include hover {
        color: $accent; } }
    li {} } }

