.section {
  padding-top: 65px;
  padding-bottom: 65px;
  @include xl {
    padding-top: 55px;
    padding-bottom: 55px; }
  @include lg {
    padding-top: 50px;
    padding-bottom: 50px; }
  @include md {
    padding-top: 40px;
    padding-bottom: 40px; }
  @include sm {
    padding-top: 30px;
    padding-bottom: 30px; }
  &__title {
    margin-bottom: 20px;
    text-align: center;
    @include md {
      margin-bottom: 15px; } }
  &__subttl {
    margin-bottom: 60px;
    text-align: center;
    max-width: 530px;
    margin-left: auto;
    margin-right: auto;
    @include xl {
      margin-bottom: 50px; }
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 20px; } }

  &--dark {
    background-color: $black-light; } }

.section-block {
  @include flex(flex-start, normal, row, wrap);
  margin-left: -15px;
  margin-right: -15px;
  &__left {
    padding-top: 35px;
    padding-left: 15px;
    padding-right: 15px;
    position: relative;
    width: percentage(5/12);
    @include before {
      background-color: $black-light;
      position: absolute;
      top: 80px;
      left: 15px;
      height: 60%;
      width: 50%;
      z-index: 0;
      @include md {
        display: none; } }
    @include after {
      @include image('black-pattern.png');
      background-repeat: repeat;
      background-size: auto;
      position: absolute;
      z-index: 1;
      top: 0;
      height: 60%;
      width: 50%;
      left: 75px;
      @include md {
        display: none; } }
    @include lg {
      width: percentage(6/12);
      padding-top: 15px; }
    @include sm {
      width: 100%;
      padding-top: 0; } }

  &__right {
    padding-top: 35px;
    padding-left: 15px;
    padding-right: 15px;
    width: percentage(6/12);
    margin-left: percentage(1/12);
    @include flex(center, normal, column);
    @include lg {
      margin-left: 0; }
    @include sm {
      width: 100%; } }
  &__img {
    @include cover;
    padding-top: 111%;
    z-index: 3;
    margin-left: percentage(1/5);
    @include md {
      margin-left: 0; } }
  &__form {
    max-width: 330px;
    @include sm {
      margin-left: auto;
      margin-right: auto; } } }
